.icon {
	min-width: 80px;
	min-height: 80px;
	border-radius: 50%;
	margin: 0 2%;
	display: flex;
	align-items: center;
	justify-content: center;
	border: 2px solid gray;
}

.OX {
	display: flex;
	width: 100%;
	justify-content: center;
	align-items: center;
	margin: 0 0 4% 0;
}
.guesslist {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.guess {
	width: 80%;
	display: flex !important;
	align-items: center;
	font-size: 1rem !important;
}
.pic {
	min-width: 40px;
	min-height: 40px;
	margin-right: 10px;
	border-radius: 50%;
}
.right {
	background-color: #78beffd7;
}
.wrong {
	background-color: gainsboro;
}
.text {
	text-align: center;
	color: #fff;
	font-weight: 700;
	text-shadow: -1px 0px 0px #000, 1px 0px 0px #000, 0px -1px 0px #000, 0px 1px 0px #000;
	font-size: 1rem !important;
}
p.text {
	font-size: 1.2rem !important;
	margin-bottom: 20px;
}
.hint {
	margin-right: 10px;
}
progress {
	margin: 2% 0;
}
